.wadi_admin_general_page .wadi_general_admin_heading {
  font-size: 2rem;
  line-height: 2;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  gap: 1rem;
  padding-right: 12px;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes .wadi_general_box {
  background-color: #e7e7e7;
  padding: 32px;
  border: 1px solid #ccc;
  border-radius: 8px;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes .wadi_general_box .wadi_addons_general_box_heading {
  font-size: 1.4rem;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes .wadi_general_box .wadi_addons_general_box_content {
  padding: 12px 0;
  font-size: 1rem;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes .wadi_general_box .wadi_addons_general_button {
  background-color: #00c9a9;
  border: 0;
  border-radius: 16px;
  padding: 12px;
  color: white;
  font-size: 1rem;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes .wadi_general_box .wadi_addons_general_url {
  background-color: #00c9a9;
  border: 0;
  border-radius: 16px;
  padding: 12px;
  color: white;
  font-size: 1rem;
  text-decoration: none;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes .wadi_general_box .wadi_addon_newsletter_subscribe_btn {
  background-color: #00c9a9;
  border: 0;
  border-radius: 0 16px 16px 0;
  color: #fff;
  font-size: 1rem;
  padding: 12px;
  margin-left: -4px;
}
.wadi_admin_general_page .wadi_addons_admin_general_boxes .wadi_general_box .wadi_newsletter_form #wadi_addons_newsletter {
  box-shadow: 0 0 0 transparent;
  border-radius: 16px 0 0 16px;
  border: 0px solid #8c8f94;
  background-color: #fff;
  color: #2c3338;
  height: 47px;
  width: 250px;
  padding-left: 20px;
}